<p>##docker 调试</p>
<ul>
<li>换源</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token comment">#vi /etc/docker/daemon.json</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><div class="language-json ext-json line-numbers-mode"><pre v-pre class="language-json"><code><span class="token punctuation">{</span>
<span class="token property">"registry-mirrors"</span><span class="token operator">:</span><span class="token punctuation">[</span><span class="token string">"https://registry.docker-cn.com"</span><span class="token punctuation">]</span>
<span class="token punctuation">}</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h3 id="问题调试" tabindex="-1"><a class="header-anchor" href="#问题调试" aria-hidden="true">#</a> 问题调试</h3>
<ul>
<li>启动时提示</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>WARNING: IPv4 forwarding is disabled. Networking will not work.
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>修改配置文件</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code><span class="token function">vim</span> /usr/lib/sysctl.d/00-system.conf
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>追加</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>net.ipv4.ip_forward<span class="token operator">=</span><span class="token number">1</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><ul>
<li>重启网络</li>
</ul>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>systemctl restart network
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="docker安装软件记录" tabindex="-1"><a class="header-anchor" href="#docker安装软件记录" aria-hidden="true">#</a> docker安装软件记录</h2>
<h3 id="redis" tabindex="-1"><a class="header-anchor" href="#redis" aria-hidden="true">#</a> Redis</h3>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>docker pull redis <span class="token comment">#拉取redis</span>
docker run -p <span class="token number">6379</span>:6379 -idt --name redis redis <span class="token comment">#启动redis</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>或直接</p>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>docker run -p <span class="token number">6379</span>:6379 -idt --name redis redis <span class="token comment">#启动redis</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>以下省略详细步骤</p>
<h2 id="mysql" tabindex="-1"><a class="header-anchor" href="#mysql" aria-hidden="true">#</a> MySql</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>docker run -itd <span class="token punctuation">\</span>
--name mysql <span class="token punctuation">\</span>
-p <span class="token number">3306</span>:3306 <span class="token punctuation">\</span>
--restart<span class="token operator">=</span>always <span class="token punctuation">\</span>
-e <span class="token assign-left variable">MYSQL_ROOT_PASSWORD</span><span class="token operator">=</span><span class="token string">'root'</span> <span class="token punctuation">\</span>
mysql:5.7 <span class="token punctuation">\</span>
--lower_case_table_names<span class="token operator">=</span><span class="token number">1</span>
 <span class="token comment">#启动MySQL</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br></div></div><h2 id="tomcat" tabindex="-1"><a class="header-anchor" href="#tomcat" aria-hidden="true">#</a> Tomcat</h2>
<div class="language-bash ext-sh line-numbers-mode"><pre v-pre class="language-bash"><code>docker run -idt --name tomcat -p <span class="token number">80</span>:8080 -v /server/tomcat/webapps/:/usr/local/tomcat/webapps/ tomcat
<span class="token comment">#将本机/server/tomcat/webapps/映射到容器/usr/local/tomcat/webapps/</span>
<span class="token comment">#将容器8080端口映射到本机80端口</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br></div></div><h2 id="docker指令" tabindex="-1"><a class="header-anchor" href="#docker指令" aria-hidden="true">#</a> Docker指令</h2>
<ul>
<li>
<p>菜鸟教程：命令大全</p>
</li>
<li>
<p>常用指令<br>
docker生命周期管理</p>
</li>
</ul>
<table>
<thead>
<tr>
<th>命令</th>
<th>参数</th>
<th>功能</th>
<th>示例</th>
</tr>
</thead>
<tbody>
<tr>
<td>run</td>
<td>1</td>
<td>创建一个容器</td>
<td>docker run 创建容器的镜像</td>
</tr>
<tr>
<td></td>
<td>-p</td>
<td>端口映射</td>
<td>-p 宿主端口:容器端口</td>
</tr>
<tr>
<td>-v</td>
<td></td>
<td>挂载	-v 宿主目录:容器目录</td>
<td></td>
</tr>
<tr>
<td>-i</td>
<td></td>
<td>以交互模式运行</td>
<td></td>
</tr>
<tr>
<td>-t</td>
<td></td>
<td>为容器重新分配一个伪输入终端</td>
<td></td>
</tr>
<tr>
<td>-d</td>
<td></td>
<td>后台运行容器，并返回容器ID</td>
<td></td>
</tr>
<tr>
<td>–name</td>
<td></td>
<td>为容器指定一个名称	–name 容器名字</td>
<td></td>
</tr>
<tr>
<td>–dns</td>
<td></td>
<td>容器的DNS，默认与宿主一样	–dns 8.8.8.8</td>
<td></td>
</tr>
<tr>
<td>-e</td>
<td></td>
<td>传递环境变量	-e MYSQL_ROOT_PASSWORD=123456</td>
<td></td>
</tr>
<tr>
<td>start</td>
<td></td>
<td>启动一个容器	docker start 容器名称或ID</td>
<td></td>
</tr>
<tr>
<td>stop</td>
<td></td>
<td>停止一个容器	docker stop 容器名称或ID</td>
<td></td>
</tr>
<tr>
<td>restart</td>
<td></td>
<td>重启一个容器	docker restart 容器名称或ID</td>
<td></td>
</tr>
<tr>
<td>rm</td>
<td></td>
<td>删除一个容器	docker rm 容器名称或ID</td>
<td></td>
</tr>
<tr>
<td>-f</td>
<td></td>
<td>通过SIGKILL强行删除一个运行中的容器</td>
<td></td>
</tr>
<tr>
<td>-l</td>
<td></td>
<td>移除容器的网络连接而非容器本身</td>
<td></td>
</tr>
<tr>
<td>-v</td>
<td></td>
<td>删除与容器关联的卷</td>
<td></td>
</tr>
<tr>
<td>pause</td>
<td></td>
<td>暂停容器</td>
<td></td>
</tr>
<tr>
<td>unpause</td>
<td></td>
<td>恢复容器</td>
<td></td>
</tr>
<tr>
<td>exec</td>
<td></td>
<td>在运行的容器中执行命令	docker exec -i -t 容器名称或ID /bin/bash</td>
<td></td>
</tr>
<tr>
<td>-i</td>
<td></td>
<td>即使没有附加也保持STDIN 打开</td>
<td></td>
</tr>
<tr>
<td>-d</td>
<td></td>
<td>在后台运行</td>
<td></td>
</tr>
<tr>
<td>-t</td>
<td></td>
<td>分配一个伪终端</td>
<td></td>
</tr>
